(function($) {
  $.fn.krSelect = function() {
    var dataYamSelect = this;

    dataYamSelect.each(function() {

      if ($(this).attr('data-kr-select') == 'created') {
        //console.log('was created, go to next');
      }

      else {
        //console.log('find not created, working...');
        var options = $(this).find('select option');
        //console.log(options);
        $(this).append('<span class="value"></span>');
        $(this).append('<ul class="select"></ul>');

        var span = $(this).find('span.value');
        var ul = $(this).find('ul.select');

        var maximumOfItems = $(this).data('max-items');

        var Core = {
          showList: function() {
            //console.log(options.length);
            var H = 0;
            var li = ul.find('li');

            setTimeout(function() {
              if(maximumOfItems && maximumOfItems < li.length) {
                for (var i = 0; i < maximumOfItems; i++) {
                  H += $(li[i]).outerHeight();
                }
                //console.log(maximumOfItems, H);
                ul.css({
                  maxHeight: H,
                  overflowY: 'hidden'
                });
              }
            }, 100);

            ul.slideDown(200, function() {
              if(maximumOfItems && maximumOfItems < li.length) {
                //console.log(maximumOfItems, H);
                ul.css({
                  height: H,
                  overflowY: 'scroll'
                });
              }
            });
          },
          hideList: function() {
            ul.slideUp(200);
          }
        };

        $(this).find('select').css('display', 'none');

        if($(this).find('select option:selected').length) {
          span.text($(this).find('select option:selected').text());
        }
        else {
          span.text($(options[0]).text());
        }


        for (var i = 0; i < options.length; i++) {
          // console.log($(options[i]));
          if($(options[i]).attr("data-prop-style")) {
            ul.append('<li class="option" data-prop-style="'+ $(options[i]).attr('data-prop-style') +'" data-value="'+ $(options[i]).attr('value') +'">'+ $(options[i]).text() +'</li>');
          }
          else {
            ul.append('<li class="option" data-value="'+ $(options[i]).attr('value') +'">'+ $(options[i]).text() +'</li>');
          }
        }

        span.bind('click', function() {
          var container = $(this).closest('[data-kr-select]');
          if(container.hasClass('active')) {
            container.removeClass('active');
            Core.hideList();
          }
          else {
            $("[data-kr-select]").each(function() {
              $(this).find(".select").stop().slideUp(200, function() {
                $(this).parent().removeClass("active");
              });
            });
            container.addClass('active');
            Core.showList();
          }
        });

        ul.find('li').bind('click', function() {
          var container = $(this).closest('[data-kr-select]');

          container.find('.value').text($(this).text());

          container.find("option").each(function() {
            $(this)[0].selected = false;
          });

          //container.find('select option[value="'+ $(this).data('value') +'"]')[0].selected = true;

          container.find('select').trigger('change');

          container.removeClass('active').find('.select').slideUp(200);
        });

        $(document).click(function(e) {
          if(!$(e.target).closest('[data-kr-select]').length) {
            dataYamSelect.each(function() {
              $(this).removeClass('active').find('.select').slideUp(200);
            });
          }
        });

        $(this).attr('data-kr-select', 'created');
        $(this).addClass('show');
      }
    });
  };
})($);


$('body').on("ajaxResponse", function() {
  var dataKrSelect = $('[data-kr-select]');
  if(dataKrSelect.length) {
    dataKrSelect.krSelect();
  }
});
